<template>
  <div class="todo_inputs">
    <div class="todo_input_field">
      <input
        type="text"
        placeholder="Enter new todo"
        v-model="text"
        @input="setSearchParam"
      />
      <div class="icon theme" title="theme">
        <img src="@/assets/icons/sun.svg" alt="no-dark-theme" />
      </div>
      <div
        class="icon search"
        :class="{ enabled: this.getIsSearchEnabled }"
        @click="this.toggleSearchEnabled"
        title="search"
      >
        <img src="@/assets/icons/loupe.svg" alt="no-seacrch-here" />
      </div>
    </div>
    <button @click="addNewTodo" class="add_todo_btn">
      <!-- add images here -->
      <p>New Todo</p>
    </button>
  </div>
</template>

<script>
import { mapGetters, mapMutations } from "vuex";

import shortid from "shortid";

export default {
  name: "TodoInput",
  data() {
    return {
      text: "",
    };
  },
  methods: {
    ...mapMutations(["addTodo", "toggleSearchEnabled", "changeSearchParam"]),
    addNewTodo() {
      if (this.text)
        this.addTodo({
          id: shortid.generate(),
          text: this.text,
          isCompleted: false,
        });
      this.text = "";
    },
    setSearchParam() {
      if (this.getIsSearchEnabled) {
        this.changeSearchParam(this.text);
      }
    },
  },
  computed: {
    ...mapGetters(["getIsSearchEnabled"]),
  },
};
</script>

<style lang="scss" scoped>
.todo_inputs {
  display: flex;
  align-items: center;
  justify-content: center;

  padding: 10px 0;

  .todo_input_field {
    width: 70%;

    position: relative;

    input {
      width: 100%;

      padding: 14px 90px 14px 0px;

      border: none;
      border-radius: 4px;
      outline: none;

      font-family: $montserrat;
      font-size: 1rem;
      text-indent: 12px;
    }
  }

  .add_todo_btn {
    margin-left: 10px;
    padding: 12px 2%;

    border: none;
    border-radius: 4px;
    outline: none;

    font-family: $montserrat;
    font-size: 1rem;
  }

  .icon {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);

    width: 32px;
    height: 32px;
    background: #ccc;
    border-radius: 8px;

    display: grid;
    place-items: center;

    &.theme {
      right: 10px;
      img {
        width: 20px;
      }
    }

    &.search {
      right: 50px;

      &.enabled {
        background: crimson;
      }

      img {
        width: 18px;
      }
    }
  }
}
</style>