import { createStore } from "vuex";

export default createStore({
  state: {
    todos: [
      { id: 12, text: "hello", isCompleted: false },
      { id: 124, text: "world", isCompleted: true },
    ],
    isSearchEnabled: false,
    searchParam: "",
  },
  getters: {
    getTodos: (state) => (text) => {
      return text === ""
        ? state.todos
        : state.todos.filter((todo) => todo.text.match(text));
    },
    countCompleted(state) {
      return state.todos.filter((todo) => todo.isCompleted).length;
    },
    getIsSearchEnabled(state) {
      return state.isSearchEnabled;
    },
    getSearchParam(state) {
      return state.searchParam;
    },
  },
  mutations: {
    addTodo(state, todo) {
      state.todos = [...state.todos, todo];
    },
    toggleIsCompleted(state, id) {
      state.todos = state.todos.map((todo) =>
        todo.id === id ? { ...todo, isCompleted: !todo.isCompleted } : todo
      );
    },
    changeText(state, info) {
      state.todos = state.todos.map((todo) => {
        if (todo.id === info.id && todo.text !== info) {
          todo.text = info.text;
        }
        return todo;
      });
    },
    moveTodo(state, info) {
      var elementToMove = state.todos.find(({ id }) => id == info.dragItemId);
      var toIndex = state.todos.findIndex(({ id }) => id == info.dropAreaId);

      state.todos.splice(state.todos.indexOf(elementToMove), 1);
      state.todos.splice(toIndex, 0, elementToMove);
    },
    deleteTodo(state, id) {
      state.todos = state.todos.filter((todo) => todo.id !== id);
    },
    toggleSearchEnabled(state) {
      state.isSearchEnabled = !state.isSearchEnabled;
    },
    changeSearchParam(state, text) {
      state.searchParam = text;
    },
  },
});
