<template>
  <div class="todo_list">
    <todo-list-item v-for="todo in todos" :key="todo.id" :todo="todo" />
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
import TodoListItem from "./TodoListItem.vue";
export default {
  name: "TodoList",
  components: {
    TodoListItem
  },
  computed: {
    ...mapGetters(["getTodos", "getIsSearchEnabled", "getSearchParam"]),
    todos() {
      return this.getIsSearchEnabled ? this.getTodos(this.getSearchParam) : this.getTodos("");
    }
  }
};
</script>

<style lang="scss" scoped>
.todo_list {
  display: flex;
  align-items: center;
  flex-direction: column;
}

</style>
