<template>
  <div
    class="todo_droparea"
    @drop="dropTodo($event)"
    @dragenter.prevent
    @dragover.prevent
  >
    <div
      class="todo"
      draggable="true"
      @dragstart="dragStart($event, todo.id)"
      :class="{ completed: todo.isCompleted, not_completed: !todo.isCompleted }"
    >
      <input
        type="checkbox"
        name=""
        :checked="todo.isCompleted"
        @click="this.toggleIsCompleted(todo.id)"
      />
      <input
        type="text"
        v-model="text"
        :disabled="!isEditing"
        class="todo_text"
      />
      <button
        @click="editTodoText"
        :disabled="todo.isCompleted"
        class="btn edit"
      >
        {{ isEditing ? "Save" : "Edit" }}
      </button>
      <button @click="this.deleteTodo(todo.id)" class="btn delete">
        Delete
      </button>
    </div>
  </div>
</template>

<script>
import { mapMutations } from "vuex";
export default {
  name: "TodoListItem",
  data() {
    return {
      text: "",
      isEditing: false,
    };
  },
  props: ["todo"],
  methods: {
    ...mapMutations([
      "toggleIsCompleted",
      "changeText",
      "deleteTodo",
      "moveTodo",
    ]),
    editTodoText() {
      if (this.isEditing) {
        console.log(this.text);
        this.changeText({ id: this.todo.id, text: this.text });
      }
      this.isEditing = !this.isEditing;
    },
    dragStart(event, id) {
      event.dataTransfer.dropEffect = "move";
      event.dataTransfer.effectAllowed = "move";
      event.dataTransfer.setData("todoId", id);
    },
    dropTodo(event) {
      var dragItemId = event.dataTransfer.getData("todoId");
      this.moveTodo({ dragItemId, dropAreaId: this.todo.id });
    },
    setCompleted() {
      (this.todo.isCompleted && this.isEditing) && this.editTodoText()
    }
  },
  mounted() {
    this.text = this.todo.text;
  },
};
</script>

<style lang="scss" scoped>
.todo_droparea {
  width: 80%;

  border-bottom: 1px solid rgb(184, 184, 184);

  .todo {
    width: 100%;
    padding: 16px 12px;

    background: #ebebeb;

    display: flex;
    align-items: center;

    position: relative;

    .todo_text {
      width: 50%;

      position: relative;

      margin-left: 4px;
      padding: 6px 0;

      background: rgb(216, 216, 216);
      border: none;
      outline: none;

      font-family: $montserrat;
      font-size: 0.95rem;
      text-indent: 8px;

      text-overflow: ellipsis;

      &:disabled {
        background: #ebebeb;
      }
    }

    .btn {
      padding: 6px 10px;

      border: none;
      outline: none;

      font-family: $montserrat;
      // font-weight: map-get($font-weights, "bold");
      letter-spacing: 1px;
      font-size: 0.95rem;
      color: white;

      &.edit {
        margin-left: auto;
        margin-right: 10px;

        background: rgb(78, 117, 231);
      }

      &.delete {
        background: crimson;
      }

      &:disabled {
        background: #ddd;

        color: grey;
      }
    }

    &::after {
      content: "";
      position: absolute;
      top: 50%;
      left: 30px;
      height: 2px;
      background: black;
      border-radius: 2px;

      // z-index: 1;
      transform: translateY(-50%);
    }

    &.not_completed::after {
      width: 0%;

      // z-index: 1;
      transform: translateY(-50%), translateX(100%);

      animation: slideOut 0.25s ease-in;
    }

    &.completed::after {
      width: 48%;
      animation: slideIn 0.25s ease-in;
    }

    @keyframes slideIn {
      0% {
        width: 0%;
      }

      100% {
        width: 48%;
      }
    }

    @keyframes slideOut {
      0% {
        width: 48%;
        left: 30px;
        // transform: translateX(0%);
      }

      100% {
        width: 0%;
        // transform: translateX(100%);
        left: calc(48% + 30px);
      }
    }
  }
}
</style>